import React from 'react';
import ReactDOM from 'react-dom';
import './App.css';
import defaultExport   from './react-transition-group';
import registerServiceWorker from './registerServiceWorker';
import {tree} from './Content/TREE.js';



function Story(props){
    var classes="";
    if (props.weather == null){
        //return nothing if the element is not active
         return (null);
     }
    else{
   
     if(props.weather == "Active Child") {
         //add styles 
          classes += "square ";
          classes +=props.pos;
         return (
             //render name if parent is clicked on
          <div className={classes} onClick={props.onClick}>
            <h1>{props.name}</h1>
           </div>
         );
            }
    else{
        //add styles
        classes+= "contentHeader ";
        classes+= props.pos;
        return(
            //render a header with two columns when selected
        <div className="content">
        <div className={classes} onClick={props.onClick}>
          <h1>{props.name}</h1> 
          <h1>Back</h1>
        </div>
        <div className = "contentBody">   
            <div className = "contentCol">
                {props.column1}
            </div>    
            <div className = "contentCol">
                {props.column2}
            </div>   
        </div>
        </div>
        );
     }
    }
}

function Cloud(props) {
     var classes ="";
     if (props.weather == null){
         //render nothing if not active
         return (null);
     }
    else {
       classes += "square ";
       classes+=props.pos;
       if(props.weather=="Active Child"){
        classes+=" Canopy";
                    //render a name if parents is active (most likey None so it will render at the start)

        return(
         <div className={classes} onClick={props.onClick}>
            <h1>{props.name}</h1>
         </div>
        );
       }   else{
            //displays as a back button when its children are being display
        return(
           
        <div className={classes} onClick={props.onClick}>
            <h1>Back </h1>
         </div>
        );
           
       }
    } 
}

function Gallery(props){
  var classes="";
    if (props.weather == null){
        //does not render when not active or a child
         return (null);
     }
    else{
   
     if(props.weather == "Active Child") {
          classes += "square ";
          classes += "Canopy ";
          classes +=props.pos;
         //renders as a child when the parent is active 
         return (
          <div className={classes} onClick={props.onClick}>
             
            <h1>{props.name}</h1>
           </div>
         );
            }
    else{
        classes+= "contentHeader ";
        classes+= props.pos;
        var  display=[];
        //iterate through the list of elements to give them all equal sized containers; all inputs MUST be responsive
        for (var x=0; props.gallery.length>x;x++){
            display.push(<div className="frame">{props.gallery[x]}</div>
            );
        }
      //create header, with styles of group class and state control

        return(
        <div className="content">
        <div className={classes} onClick={props.onClick}>
          <h1>{props.name}</h1> 
          <h1>Back</h1>
        </div>

        <div className = "contentBody">{display}</div>
        </div>
        );
     }
    }
}
     
    


class Sky extends React.Component {
  //prop.tree.map is iported from another file. it is currently a javascript object. I may look to change it based on if I create a CMS type system.
  constructor(props) {
    super(props);
    this.state = {
        //initizlize the initial state of all elements
      clouds: this.props.tree.map((weather) => {
        if(weather.parent==="None"){
            //make sure elements with no parents render on first load
            //we use an array because not other dat structure can beat O(n) due to making non-active elements disappear.
            return ("Active Child");
        }
        else{
            return null;
        }
      }
    ),
     
    };
      
  }

  handleClick(i) {
      //get the state of the clicked upon element
    const weather = this.state.clouds[i];
      //get the states of the elements set all to null for readablility. This process may need reworking if the system gets larger. 
    const clouds = this.state.clouds.slice().fill(null);
    // if the element is active move up a step
    if (weather=="Active"){
        //get the parent of the active element
        var up =this.props.tree[i].parent;
        for (let x = 0; clouds.length>x; x++){
            
            if(this.props.tree[x].parent==up){
                // set all relatives aside from parent to active
                clouds[x] = "Active Child";
                 }
            if (this.props.tree[x].name==up){
                //make the parent active
                clouds[x] = "Active";
            }
        }
    
    }
      //if a child make it active and display children (if there are any)
      //the issue is what do with the non children and non-actives, we have to touch all of them, thus an array is the best option
      //maybe there is a way we can rework this to move in and get some O(log(n)) but the 0(1) for access is nice so there are tradeoffs.
    else{
        for (let x = 0; clouds.length>x; x++){
            if(this.props.tree[x].parent==this.props.tree[i].name) {
                 
                clouds[x] = "Active Child";
        }
        }
         clouds[i] = 'Active'; 
    }
   //update state
    this.setState({clouds: clouds,
                  });
  }

  renderCloud(i) {
    //pass data through to appropraite object
    return (

      <Cloud
        name = {this.props.tree[i].name}
        weather={this.state.clouds[i]}
        onClick={() => this.handleClick(i)}
        pos = {this.props.tree[i].class}

      />
    );
  }
  renderStory(i) {
      return(
        <Story
        name = {this.props.tree[i].name}
        weather={this.state.clouds[i]}
        onClick={() => this.handleClick(i)}
        pos = {this.props.tree[i].class}
        column1 = {this.props.tree[i].column1}
        column2 = {this.props.tree[i].column2}
        />
      )
}
    renderGallery(i){
        console.log(this.props.tree[i].gallery);    
        return(
        <Gallery
        name = {this.props.tree[i].name}
        weather={this.state.clouds[i]}
        onClick={() => this.handleClick(i)}
        pos = {this.props.tree[i].class}
        gallery = {this.props.tree[i].gallery}
        />
        
        );
    }

  renderSky(){
      const sky = this.props.tree;
      var out=[];
      //go through the list and render each element as it's listed type 
      for(var i = 0; sky.length>i; i++){
          if(sky[i].type=="Cloud"){
               out.push(this.renderCloud(i));
          }
      else if(sky[i].type=="Gallery"){
          out.push(this.renderGallery(i));
      }
      
      else{
           out.push(this.renderStory(i));
      }
        
  }
      return out;     
  }
  render() {

    
    
    return (
    //construct general page structure
      <div className =  "background">
         
        <div className = "header">
         <h1> James Stuart (Work in Progress) </h1>
          
        </div>
      
        <div className = "flex">
        {this.renderSky()}
      </div>

     </div>

    );
  }
}
class App extends React.Component {
  render() {
    return (
       
          <Sky 
            tree = {tree}
          />
    );
  }
}
//link it to the DOM!
ReactDOM.render(<App />, document.getElementById('root'));
registerServiceWorker();



